const H2 = ({ children }) => (
  <Typography variant='h2' color='primary' mr={1} p={0} lineHeight={1.5}>
    {children}
  </Typography>
);

const H3 = ({ children, bold = false, noLine = false }) => (
  <Typography
    variant='h3'
    color='primary'
    fontWeight={bold ? 'bold' : 'normal'}
    sx={{
      display: 'inline-block',
      position: 'relative',
      zIndex: 0,
      '::after': noLine
        ? {}
        : {
            display: 'inline-block',
            position: 'absolute',
            content: `''`,
            left: 0,
            bottom: 0,
            height: 8,
            width: '100%',
            zIndex: -1,
            backgroundColor: 'rgba(255, 165, 0, 0.8)'
          }
    }}
  >
    {children}
  </Typography>
);

const Dot = ({ children }) => (
  <Box
    component='span'
    sx={{
      backgroundImage:
        'radial-gradient(circle at center, orange 20%, transparent 20%)',
      backgroundPosition: 'top right',
      backgroundRepeat: 'repeat-x',
      backgroundSize: '1em 0.3em',
      paddingTop: '0.1em'
    }}
  >
    {children}
  </Box>
);

const Transition = ({ children, delay = 0 }) => {
  const { ref, inView } = useInView({
    //  inview 
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

